.card{
    position: relative;
}
.card img{
    width: 100%;
    display: block;
    border: 1px solid #fff;
    border-radius: 6px;
}  

/* front of cards--the picture */

.card .front{
    transform: rotateY(90deg);
    position: absolute;
    transition: all ease-in 0.2s;

}
.flipped .front{
    transform: rotateY(0deg);
    height: 100%;
    transition-delay: 0s;
}

/* back of card cover */

.card .back{
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;
}

.flipped .back{
    transform: rotateY(90deg) ;
    transition-delay: 0s;
}